.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__logo {
        width: 250px;
        img {
            width: 100%;
        }
    }

    &__official {
        text-align: center;
        font-weight: 500;
        font-size: 12px;
        color: #FFFFFF;
        span {
            display: block;
            font-size: 18px;
        }
    }

    &__phone {
        display: block;
        font-weight: 700;
        font-size: 18px;
        color: #FFFFFF;
        &:hover {
            color: #ffffff;
            text-decoration: none;
        }
    }

    &__contacts {
        display: flex;
        flex-direction: column;
        align-items:flex-end;
    }
}

    