.catalog {
    padding-bottom: 100px;
    /* /* .container {
        margin-left: 90px;
        margin-right: 90px;} */
    
    h2 {
        text-align: center;
        text-transform: uppercase;
    }
    &__tabs {
        margin-top:70px;
        list-style-type: none;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 0;
        padding-left: 0;

    }
    &__tab {
        width: 222px;
        height: 52px;
        background: rgba(0, 0, 0, 0.0001);
        border: 2px solid #D0D0D0;

        font-weight: 400;
        font-size: 18px;
        color: #6B6B6B;
        text-transform: uppercase;
        text-align: center;
        line-height: 50px;
        transform: skewX(-25deg);
        margin: 0 5px;
        cursor: pointer;
        transition: 0.5s all;
        &:hover {
            box-shadow: 3px 3px 20px  $main-color;
            border:none;
        }
        div {
            transform: skewX(25deg);
        }
        &_active {
            background: $main-color;
            color: #FFFFFF;
            border: none;
        }
        
    }

    &__content {
        margin-top: 60px;  
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        display: none;
        &_active {
            display: flex;
        }
    }
    &-item {
        width: 303px; /* залишив поки 303 щоб був марджін між картками, бо коли більше - марджін зменшується + карточки не вміщається в ряд і летять на другий */
        min-height: 442px;
        border: 2px solid #E8E8E8;
        padding: 20px 20px 26px 20px;
        overflow: hidden;
        margin-bottom: 20px;
        &__img {
            display: block;
            margin: 0 auto;
            max-width: 100%;
            height: 200px;
        }
        &__subtitle {
            margin-top: 20px;
            font-weight: 700;
            font-size: 16px;
            color: #0D0D0D;
            text-align: center;
        }
        &__descr {
            margin-top: 16px;
            font-weight: 300;
            font-size: 14px;
            color: #0D0D0D;
            text-align: center;
        }
        &__link {
            display: block;
            margin-top: 17px;
            font-weight: 500;
            font-size: 14px;
            color: $main-color;
            text-decoration: underline;
            text-transform: uppercase;
            text-align: center;
            &:hover {
                color: $main-color;
            }
        }

        hr {
            background-color: #E8E8E8;
            margin-top: 19px;
            margin-bottom: 21px;
            border-bottom: none;
        }

        &__footer {
            display: flex;
            justify-content: space-between;
            align-items: center;

        }

        &__old-price {
            font-weight: 300;
            font-size: 14px;
            color: #555555;
            text-decoration: line-through;
        }

        &__new-price {
            font-weight: 700;
            font-size: 20px;
          
      
        }

        &__wrapper {
            display: flex;
            width: calc(200% + 40px);
        }
        &__content {
            width: 50%;
            margin-right: 0px;
            padding-right: 7px; /* добавив тому що текст виходить за блок */
            transform: translateX(-100%);
            transition: 0.5s all;
            &_active {
                transform: translateX(0%);
                margin-right: 20px;
            }
        }
        &__list {
            position: relative;
            width: 50%;
            margin-left: 20px;
            transform: translateX(0%);
            transition: 0.5s all;
            padding-right: 20px;
            padding-left: 19px;
            margin-bottom: 0;
            list-style-type: none;
            /* display: none; */ /* поки що закриваємо */
            &_active {
                transform: translateX(-100%);
                margin-right: 0px;
            }
            ul {
                margin: 0;
                padding: 0;
                list-style-type: none;
            }
            li {
                position: relative;
                font-weight: 300;
                font-size: 14px;
                color: #555555;
                margin-bottom: 20px;
                &:before {
                    position: absolute;
                    content: "";
                    width: 5px;
                    height: 5px;
                    background-color: $main-color;
                    left: -14px;
                    top: 7px;

                }
            }

        }
        &__list &__back {
            /*   position: absolute; */
              bottom:0;
              /* left: 50%;
              transform: translateY(calc(-50% - 19px + 7px)); */
              display: block;
              padding-right: 39px;/* паддінг 19 плюс марджін-райт 20 в &__list*/
              font-weight: 500;
              font-size: 14px;
              color: $main-color;
              text-decoration: underline;
              text-transform: uppercase;
              text-align: center;
              &:hover {
                  color: $main-color;
              }
          }
        
    }

}