.carousel {
    padding: 81px 0;
    &__inner {
        position:relative;
        width: 750px;
        margin: 0 auto;
        }
    .prev, .next {
        position:absolute;
        top:50%;
        transform: translateY(-50%);
        left: -66px;
        border:none;
    
        img {
            width: 31px;
            height: 50px;
        }
        }
    .next {
        right: -66px;
        left:auto;
    }

    .tns-slider {
        img {
            display:block;
            margin: 0 auto;
            max-width: 100%;
            height: 540px;
        }
        
    }
   

   
}


    

