.button {
    color: #FFFFFF;
    width: 153px;
    height: 32px;
    border-radius: 5px;
    background-color: $main-color;
    border: none;

    font-weight: 500;
    font-size: 12px;
    text-transform: uppercase;
    &_main {
        margin-top: 67px;
        width: 300px;
        height: 50px;
        font-weight: 500;
        font-size: 16px;
        background-color: $main-color;
        border-radius: 5px;
        color: #FFFFFF;
        text-transform: uppercase;
    }

    &_submit {
        width: 100%;
        height: 55px;
        font-weight: 500;
        font-size: 16px;
        
    }

    &_mini {
        width: 135px;
        height: 44px;
        font-size: 14px;

    }
}